import React, { useEffect, useState } from "react";
import { Button, Card } from "react-bootstrap";
import { FaPlus } from "react-icons/fa6";
import SubscriptionModal from "./SubscriptionModal";
import SubscriptionCards from "./SubscriptionCards";
import { getAllSubscriptionPlans } from "../../../../app/features/subscriptionPlans/subscriptionPlans.slice";
import { useDispatch } from "react-redux";

export default function SubscriptionPlansList() {
  const [addPlan, setAddPlan] = useState({
    data: null,
    show: false,
  });

  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getAllSubscriptionPlans());
  }, [dispatch]);

  return (
    <>
      <Card className="shadow-sm mt-3">
        <div className="m-3">
          <div className="d-flex justify-content-end">
            <Button
              variant="primary"
              type="button"
              className="d-flex Admin-Add-btn fw-bold"
              onClick={() => setAddPlan({ show: true, data: null })}
            >
              <FaPlus size={25} className="me-2" />
              <span>Add New Plan</span>
            </Button>
          </div>
          <div className="mt-3">
            <SubscriptionCards />
          </div>
        </div>
      </Card>
      <SubscriptionModal
        data={addPlan.data}
        show={addPlan.show}
        onClose={() => setAddPlan({ show: false, data: null })}
      />
    </>
  );
}
