import React, { useEffect } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import "react-phone-input-2/lib/style.css";
import { useForm } from "react-hook-form";
import { DURATION_TYPE } from "../../../../../components/LookupData/Data";
import { useDispatch } from "react-redux";
import {
  createPlan,
  getAllSubscriptionPlans,
} from "../../../../../app/features/subscriptionPlans/subscriptionPlans.slice";

export default function SubscriptionModal({ show, onClose, data }) {
  const { register, handleSubmit, reset, setValue } = useForm();
  const dispatch = useDispatch();

  useEffect(() => {
    if (data) {
      setValue("planName", data?.item?.name || "");
      setValue("description", data?.item?.description || "");
      setValue("price", data?.item?.amount || "");
      setValue("durationTypeId", data?.item?.durationId || "");
      setValue("numberOfAppointments", data?.item?.numberOfConsultation || "");
      setValue(
        "numberOfPrescriptions",
        data?.item?.numberOfPrescriptions || ""
      );
      setValue("statusId", data?.item?.isActive === true ? "1" : "2" || "");
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);

  function onCreateSuccess() {
    reset();
    dispatch(getAllSubscriptionPlans());
  }

  function onSubmit(submittedData) {
    const finalData = {
      planId: data?.item?.planId ? data?.item?.planId : 0,
      name: submittedData?.planName,
      description: submittedData?.description,
      amount: parseInt(submittedData?.price),
      isActive: submittedData?.statusId === "1" ? true : false,
      numberOfConsultation: parseInt(submittedData?.numberOfAppointments),
      durationId: parseInt(submittedData?.durationTypeId),
      numberOfPrescriptions: parseInt(submittedData?.numberOfPrescriptions),
    };
    dispatch(createPlan({ finalData, onCreateSuccess }));
    onClose();
  }

  return (
    <>
      <Modal show={show} onHide={onClose} size="lg">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <Modal.Header closeButton>
            <Modal.Title style={{ fontSize: "19px", fontWeight: "bold" }}>
              {data ? "Update" : "Add"} Subscription Plan
            </Modal.Title>
          </Modal.Header>
          <Modal.Body className="p-4">
            <Row style={{ height: "640px", overflow: "hidden scroll" }}>
              <Col lg={12}>
                <Form.Group className="mb-3" controlId="subscriptionPlan">
                  <Form.Label className="fw-bold">Subscription Plan</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Plan Name"
                    maxLength={150}
                    {...register("planName", { required: !data })}
                  />
                </Form.Group>
              </Col>
              <Col lg={12}>
                <Form.Group className="mb-3" controlId="description">
                  <Form.Label className="fw-bold">Description</Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={3}
                    maxLength={250}
                    {...register("description", { required: !data })}
                  />
                </Form.Group>
              </Col>
              <Col lg={12}>
                <Form.Group className="mb-3" controlId="price">
                  <Form.Label className="fw-bold">Price (â‚¬)</Form.Label>
                  <Form.Control
                    type="number"
                    step="0.01"
                    placeholder="Enter Price"
                    min={0}
                    {...register("price", { required: !data })}
                  />
                  <p class="input-field-below-text">
                    Amount you want to charge people who join this plan. Leave 0
                    if you want this plan to be free.
                  </p>
                </Form.Group>
              </Col>
              <Col lg={12}>
                <Form.Group className="mb-3" controlId="durationType">
                  <Form.Label className="fw-bold">Duration Type</Form.Label>
                  <Form.Select
                    {...register("durationTypeId", { required: !data })}
                  >
                    <option value="">Select Duration</option>
                    {DURATION_TYPE.map((type) => {
                      return (
                        <option value={+type?.lookupId}>{type.value}</option>
                      );
                    })}
                  </Form.Select>
                </Form.Group>
              </Col>
              <Col lg={12}>
                <Form.Group className="mb-3" controlId="numberOfAppointments">
                  <Form.Label className="fw-bold">
                    No. of Appointments
                  </Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="Appointment(s)"
                    min={0}
                    {...register("numberOfAppointments", { required: !data })}
                  />
                </Form.Group>
              </Col>
              <Col lg={12}>
                <Form.Group className="mb-3" controlId="numberOfPrescriptions">
                  <Form.Label className="fw-bold">
                    No. of Prescriptions
                  </Form.Label>
                  <Form.Control
                    type="number"
                    placeholder="Prescription(s)"
                    min={0}
                    {...register("numberOfPrescriptions", { required: !data })}
                  />
                </Form.Group>
              </Col>
              <Col lg={12}>
                <Form.Group className="mb-3" controlId="status">
                  <Form.Label className="fw-bold">Status</Form.Label>
                  <Form.Select
                    aria-label="Select Status"
                    {...register("statusId", { required: !data })}
                  >
                    <option value="">Select status</option>
                    <option value="1">Active</option>
                    <option value="2">Inactive</option>
                  </Form.Select>
                </Form.Group>
              </Col>
            </Row>
          </Modal.Body>
          <Modal.Footer className="Doctor-Modal-Footer">
            <Button onClick={onClose} className="Admin-Modal-CancelBtn fw-bold">
              Cancel
            </Button>
            <Button
              variant="primary"
              type="submit"
              className="Admin-Add-btn fw-bold"
            >
              Save Subscription Plan
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </>
  );
}

