import React, { useState } from "react";
import FullScreenLoader from "../../../../../authorization/FullScreenLoader";
import { useSelector } from "react-redux";
import { Button } from "react-bootstrap";
import SubscriptionModal from "../SubscriptionModal";

function SubscriptionCards() {
  const [updatePlan, setUpdatePlan] = useState({
    data: null,
    show: false,
  });
  const { subscriptionPlansList, isLoading, isError, isSuccess } = useSelector(
    (state) => state?.subscriptionPlans
  );

  return (
    <>
      <div className="patient__prescription-forms mb-3 custom-plans">
        {isSuccess ? (
          subscriptionPlansList?.length > 0 ? (
            subscriptionPlansList?.map((item, index) => {
              return (
                <div
                  className="container d-flex justify-content-center align-items-center flex-column"
                  key={`id-${index}`}
                >
                  <h3 className="title mt-2 fw-bold">
                    {item?.name ? item?.name : "N/A"}
                  </h3>
                  <h3 className="price">
                    ${item?.amount ? item?.amount : "N/A"}
                    <span> / {item?.duration ? item?.duration : "N/A"}</span>
                  </h3>
                  <p className="description">
                    {item?.description ? item?.description : "N/A"}
                  </p>
                  <p className="m-0">
                    <b>No. of Appointment(s):</b>{" "}
                    {item?.numberOfConsultation
                      ? item?.numberOfConsultation
                      : "N/A"}
                  </p>
                  <p className="m-0">
                    <b>No. of Prescription(s):</b>{" "}
                    {item?.numberOfPrescriptions
                      ? item?.numberOfPrescriptions
                      : "N/A"}
                  </p>
                  <Button
                    className="subscribe-button"
                    onClick={() => setUpdatePlan({ show: true, data: {item} })}
                  >
                    Edit
                  </Button>
                  <div className="ribbon-wrap">
                    <div
                      className={`ribbon ${
                        item?.isActive ? "active-status" : "inactive-status"
                      }`}
                    >
                      {item?.isActive ? "Active" : "InActive"}
                    </div>
                  </div>
                </div>
              );
            })
          ) : (
            <p className="text-center mt-3">No Record Found</p>
          )
        ) : isLoading ? (
          <FullScreenLoader />
        ) : isError ? (
          <p className="text-center mt-3">Network Error...</p>
        ) : null}
      </div>
      <SubscriptionModal
        data={updatePlan.data}
        show={updatePlan.show}
        onClose={() => setUpdatePlan({ show: false, data: null })}
      />
    </>
  );
}

export default SubscriptionCards;
